
/**
 * @imports
 */
import Observer from '@web-native-js/observer';

var wn;
if (typeof WebNative !== 'undefined') {
    wn = WebNative;
} else if (typeof process !== 'undefined') {
    if (!process.env.WebNative) {
        process.env.WebNative = {};
    }
    wn = process.env.WebNative;
}
wn.Observer = Observer;