
/**
 * @imports
 */
import Jsen from '@web-native-js/jsen';
import _isTypeObject from '@web-native-js/commons/js/isTypeObject.js';
import _isFunction from '@web-native-js/commons/js/isFunction.js';
import _isNumeric from '@web-native-js/commons/js/isNumeric.js';
import _isString from '@web-native-js/commons/js/isString.js';
import _isArray from '@web-native-js/commons/js/isArray.js';
import _arrFrom from '@web-native-js/commons/arr/from.js';
import _unique from '@web-native-js/commons/arr/unique.js';
import _following from '@web-native-js/commons/arr/following.js';
import _before from '@web-native-js/commons/str/before.js';
import _after from '@web-native-js/commons/str/after.js';
import { window, trap, params } from './ENV.js';
import createBundleMatrix from './createBundleMatrix.js';
import './customElements.js';

// ------------------
// ready
// ------------------
const ready = new Promise(resolve => {
    if (window.document.readyState === 'complete') {
        resolve(window.document); return;
    }
    window.document.addEventListener('DOMContentLoaded', () => resolve(window.document), false);
    window.addEventListener('load', () => resolve(window.document), false);
});

// ------------------
// bundles
// bundles.ready
// ------------------
const bundles = createBundleMatrix(ready.then(() => {
    var bundles = _arrFrom(window.document.querySelectorAll('template[is="' + params.bundleElement + '"]')).reverse();
    return bundles.map(b => {
        if (b.hasAttribute('src') && !b.content.children.length) {
            return new Promise(resolve => {
                b.addEventListener('bundleloadsuccess', () => resolve(b));
                b.addEventListener('bundleloaderror', () => resolve(b));
            });
        }
        return b;
    });
}), (...args) => inherit(...args));

/**
 * @exports - others
 */
export {
    ready,
    bundles,
};

/**
 * Composes a component from a template component.
 *
 * @param Element				    el
 * @param Element				    sourceEl
 * @param array						norecompose
 *
 * @return Element
 */
export function recompose(el, sourceEl, norecompose = []) {
    if (sourceEl.matches(params.recomposeElement)) {
        return inherit(sourceEl, el, norecompose, false/*cloneSource*/);
    }
    return el;
};

/**
 * Composes a component from a super component.
 *
 * All definitions will be inherited.
 * If the idea is to import, the super component's element will be returned,
 * (On import, nodes in component (as defined, if) will be uploaded into slots in the super component.)
 *
 * @param Element				    el
 * @param Element				    sourceEl
 * @param array						norecompose
 * @param bool						cloneSource
 *
 * @return Element
 */
export function inherit(el, sourceEl, norecompose = [], cloneSource = true) {
    if (el.matches(params.importElement) || el.matches(params.recomposeElement)) {
        var importEl = el, clonedSourceEl = !cloneSource ? sourceEl : sourceEl.cloneNode(true);
        // replacementEl needs to inherit attributes and scoped script from importEl
        importNodes(clonedSourceEl, importEl);
        importAttributes(clonedSourceEl, importEl, norecompose);
        importScript(clonedSourceEl, importEl, norecompose);
        return clonedSourceEl;
    }
    // We will append defs from the sourceEl into el
    importAttributes(el, sourceEl, norecompose, false/*prioritize*/);
    importScript(el, sourceEl, norecompose, false/*prioritize*/);
    return el;
};

/**
 * Imports nodes from from sourceEl into el.
 *
 * @param Element				    el
 * @param Element				    sourceEl
 *
 * @return Element
 */
export function importNodes(el, sourceEl) {
    if (!params.SCOPED_HTML) {
        return el;
    }
    // -------------------------
    // Upload nodes into el just the way slots work in Web Compoonents
    // -------------------------
    var elIsRoot = el.hasAttribute(params.SCOPED_HTML.rootAttribute) || el.matches('html');
    _arrFrom((sourceEl.shadowRoot || sourceEl).children).forEach((replacementNode, i) => {
        if (params.SCOPED_JS && replacementNode.matches(params.SCOPED_JS.scriptElement)) {
            return;
        }
        replacementNode = replacementNode.cloneNode(true);
        var scopedID, CSSEscape = window.CSS ? window.CSS.escape : str => str;
        if (elIsRoot && (scopedID = replacementNode.getAttribute(params.SCOPED_HTML.scopedIdAttribute))) {
            var originalNode,
                rootSelector = '[' + CSSEscape(params.SCOPED_HTML.rootAttribute) + '],html',
                originalNodeSelector = '[' + CSSEscape(params.SCOPED_HTML.scopedIdAttribute) + '="' + scopedID + '"]';
            if ((el.shadowRoot && (originalNode = el.shadowRoot.querySelector(originalNodeSelector)))
            || ((originalNode = el.querySelector(originalNodeSelector)) && originalNode.parentNode.closest(rootSelector) === el)) {
                // Replace or recompose in-place?
                if (replacementNode.matches(params.recomposeElement)) {
                    // originalNode needs to inherit attributes and scoped script from replacementNode
                    importAttributes(originalNode, replacementNode);
                    importScript(originalNode, replacementNode);
                    // Recompose in-place
                    importNodes(originalNode, replacementNode);
                } else {
                    // Just before we replace,
                    // replacementNode needs to inherit attributes and scoped script from originalNode
                    importAttributes(replacementNode, originalNode, [], false/*prioritize*/);
                    importScript(replacementNode, originalNode, [], false/*prioritize*/);
                    // Port to target...
                    originalNode.replaceWith(replacementNode);
                }
            } else {
                //throw new Error('Composition Error: Node #' + i + ' (at ' + sourceElNs + ') must match exactly one targetNode in ' + elNs + '! (' + originalNodes.length + ' matched)');
                el.append(replacementNode);
            }
        } else {
            el.append(replacementNode);
        }
    });
    return el;
};

/**
 * Imports attributes from sourceEl into el.
 *
 * @param Element				    el
 * @param Element				    sourceEl
 * @param array						norecompose
 * @param bool						prioritize
 *
 * @return Element
 */
export function importAttributes(el, sourceEl, norecompose = [], prioritize = true) {
    // ----------------------------
    // Norecompose directive
    // ----------------------------
    norecompose = norecompose.concat(params.norecomposeAttributes);
    if (!prioritize) {
        norecompose = norecompose.concat(params.namespaceAttribute);
    }
    if (el.hasAttribute('norecompose')) {
        norecompose = norecompose.concat((el.getAttribute('norecompose') || '*').split(' ').map(val => val.trim()));
    }
    // ----------------------------
    // Custom Composition...
    // ----------------------------
    if (_isFunction(params.recomposeCallback)) {
        var disposition = params.recomposeCallback(el, sourceEl, norecompose);
        if (disposition === false) {
            return false;
        } else if (_isString(disposition) || _isArray(disposition)) {
            norecompose = norecompose.concat(disposition);
        }
    }
    // ----------------------------
    // Merge list attributes...
    // ----------------------------
    var listAttributes = params.listAttributes.concat(['role', 'class']);
    if (params.SCOPED_HTML) {
        listAttributes.push(params.SCOPED_HTML.idHintsAttribute);
    }
    _unique(listAttributes).forEach(type => {
        var b_attr, a_attr;
        if (!norecompose.includes(type) && !norecompose.includes('*') && (b_attr = sourceEl.getAttribute(type))) {
            if (a_attr = el.getAttribute(type)) {
                var jointList = !prioritize ? [b_attr, a_attr] : [a_attr, b_attr];
            } else {
                var jointList = [b_attr];
            }
            el.setAttribute(type, _unique(jointList.join(' ').split(' ').map(r => r.trim())).join(' '));
            norecompose.push(type);
        }
    });
    // ----------------------------
    // Merge key/val attributes...
    // ----------------------------
    _unique(params.keyValAttributes.concat('style')).forEach(type => {
        var b_attr, a_attr;
        if (!norecompose.includes(type) && !norecompose.includes('*') && (b_attr = sourceEl.getAttribute(type))) {
            if (a_attr = el.getAttribute(type)) {
                var jointDefs = !prioritize ? [b_attr, a_attr] : [a_attr, b_attr];
                if (!jointDefs[0].trim().endsWith(';')) {
                    jointDefs[0] = jointDefs[0] + ';';
                }
            } else {
                var jointDefs = [b_attr];
            }
            el.setAttribute(type, jointDefs.join(' '));
            norecompose.push(type);
        }
    });
    // ----------------------------
    // Port all other attributes...
    // ----------------------------
    var priorityAttrs = [params.namespaceAttribute];
    if (params.SCOPED_HTML) {
        priorityAttrs.push(params.SCOPED_HTML.rootAttribute, params.SCOPED_HTML.scopedIdAttribute);
    }
    if (!norecompose.includes('*')) {
        for (var i = 0; i < sourceEl.attributes.length; i ++) {
            var attr = sourceEl.attributes[i];
            if (!norecompose.includes(attr.name) 
            && (!el.hasAttribute(attr.name) || priorityAttrs.includes(attr.name))) {
                el.setAttribute(attr.name, attr.value);
            }
        }
    }
    return el;
};

/**
 * Imports script from sourceEl into el.
 *
 * @param Element				    el
 * @param Element				    sourceEl
 * @param array					    norecompose
 * @param bool					    prioritize
 *
 * @return Element
 */
export function importScript(el, sourceEl, norecompose = [], prioritize = true) {
    // ----------------------------
    // Norecompose directive
    // ----------------------------
    if (el.hasAttribute('norecompose')) {
        norecompose = norecompose.concat((el.getAttribute('norecompose') || '*').split(' ').map(val => val.trim()));
    }
    // ----------------------------
    // For data blocks...
    // ----------------------------
    if (!norecompose.includes('--scoped-js') && !norecompose.includes('*') && params.SCOPED_JS) {
        var elDefs = _arrFrom((el.shadowRoot || el).children)
            .filter(node => node.matches(params.SCOPED_JS.scriptElement));
        var sourceElDefs = _arrFrom((sourceEl.shadowRoot || sourceEl).children)
            .filter(node => node.matches(params.SCOPED_JS.scriptElement));
        if (sourceElDefs.length) {
            if (elDefs.length) {
                elDefs[0][!prioritize ? 'prepend' : 'append'](sourceElDefs[0].textContent);
            } else {
                el.prepend(sourceElDefs[0].cloneNode(true));
            }
        }
    }
    return el;
};

/**
 * Parses an element's CHTML namespace.
 * This explains how an element's namespace is used in CHTML.
 *
 * @param string					namespaceStr
 *
 * @return object
 */
export function parseNamespace(namespaceStr) {
    var namespaceParse = {roadmap:namespaceStr};
    if (namespaceParse.roadmap) {
        namespaceParse.namespace = _before(namespaceParse.roadmap, '//');
        namespaceParse.subnamespace = _after(namespaceParse.roadmap, '//');
        // In case this is the /// spot...
        if (namespaceParse.subnamespace.startsWith('/')) {
            namespaceParse.subnamespace = _after(namespaceParse.subnamespace, '/');
        }
        if (namespaceParse.subnamespace.endsWith('//') && namespaceParse.subnamespace.indexOf('///') === -1) {
            namespaceParse.subnamespace = namespaceParse.subnamespace + namespaceParse.namespace + '//';
        }
    }
    return namespaceParse;
};

/**
 * Binds a (reactive) list context to the instance.
 * Childnodes will be automatically created/removed per key.
 *
 * @param Element 		el
 * @param array 		items
 * @param string 		subnamespace
 * @param function 		itemizeCallback
 *
 * @return trap.MutationEvent
 */
export function itemize(el, items, subnamespace, itemizeCallback = null) {
    var scopedHTML = el['.scopedHTML'];
    // --------------
    var nodeNamespaceArray = subnamespace.split('//');
    // Create a namespace hash...
    if (nodeNamespaceArray[0].indexOf('[') > -1) {
        nodeNamespaceArray[0] = '"' + nodeNamespaceArray[0].replace(/\[/g, '" + ').replace(/\]/g, ' + "') + '"';
    }
    var itemsKeys = trap.keys(items);
    var currentNodeNames = trap.keys(scopedHTML.store);
    // --------------
    var e = new trap.MutationEvent(el, {type:'remodelling'});
    _unique(itemsKeys.concat(currentNodeNames)).forEach(nodeName => {
        nodeName = _isNumeric(nodeName) ? parseInt(nodeName) : nodeName;
        var existingNode = scopedHTML.find(nodeName);
        var rspns;
        if (trap.has(items, nodeName)) {
            var srcItem = trap.get(items, nodeName), isNewNode = false;
            if (!existingNode) {
                // --------------
                var nodeNamespaceArrayCopy = nodeNamespaceArray.slice();
                if (nodeNamespaceArrayCopy[0].indexOf('"') > -1) {
                    nodeNamespaceArrayCopy[0] = nodeNamespaceArrayCopy[0].replace(/" \+  \+ "/g, nodeName);
                    nodeNamespaceArrayCopy[0] = Jsen.parse(nodeNamespaceArrayCopy[0]).eval(srcItem);
                }
                var nodeEl = partialsImport(nodeNamespaceArrayCopy.join('//'));
                // --------------
                if (nodeEl) {
                    var following = _following(itemsKeys, nodeName + ''/*numeric nodeName needs this*/, true/*length*/)
                        .reduce((closest, _nodeName) => closest || scopedHTML.find(_nodeName), null);
                    if (following) {
                        following.before(nodeEl);
                    } else {
                        el.append(nodeEl);
                    }
                    existingNode = scopedHTML.add(nodeName, nodeEl);
                    isNewNode = true;
                }
            }
            if (existingNode) {
                if (_isFunction(itemizeCallback)) {
                    rspns = itemizeCallback(nodeName, existingNode, srcItem, isNewNode);
                }
            }
        } else if (existingNode) {
            if (_isFunction(itemizeCallback)) {
                rspns = itemizeCallback(nodeName, existingNode);
            }
            var remove = () => {
                existingNode.remove();
            };
            if (rspns instanceof Promise) {
                rspns.then(remove).catch(remove);
            } else {
                remove();
            }
        }
        e.response(rspns);
    });
    return e;
};

if (params.SCOPED_HTML) {
    /**
     * Traps new nodes in ScopedHTML
     * to automatically resolve them by HTML partials.
     *
     * @param Element 		newNode
     * @param ScopedHTML 	scopedHTML
     *
     * @return Element
     */
    params.SCOPED_HTML.addCallback = (newNode, scopedHTML) => {
        var _newNode;
        if (newNode.matches(params.importElement) && (_newNode = newNode.resolve())) {
            return _newNode;
        }
        return newNode;
    };
}

if (params.SCOPED_JS) {
    /**
     * Binds a (reactive) list context to the instance.
     * Childnodes will be automatically created/removed per key.
     *
     * @param Element 		el
     * @param object 		binding
     * @param object 		_binding
     *
     * @return trap.MutationEvent
     */
    params.SCOPED_JS.bindCallback = (el, binding, _binding) => {
        const namespaceParse = parseNamespace(el.getAttribute(params.namespaceAttribute) || '');
        const itemizeCallback = params.itemizeCallback || function(nodeName, node, itemizeItem, isExistingNode = false) {
            if (arguments.length > 2) {
                return node.bind(itemizeItem);
            } else {
                return node.unbind();
            }
        };
        if (namespaceParse.subnamespace) {
            if (_isTypeObject(binding) && binding) {
                // Mirror
                trap.observe(binding, changes => {
                    return itemize(el, binding, namespaceParse.subnamespace, itemizeCallback);
                }, {tags: ['#HTMLPartials-itemize', el]});
            }
            if (_isTypeObject(_binding) && _binding) {
                // Unmirror
                trap.unobserve(_binding, null, null, {tags: ['#HTMLPartials-itemize', el]});
            }
            // Initial Sync...
            return itemize(el, binding || {}, namespaceParse.subnamespace, itemizeCallback);
        }
    };
}
