
/**
 * @imports
 */
import * as SCOPED_HTML from './scoped-html/ENV.js';
import * as SCOPED_JS from './scoped-js/ENV.js';
import * as HTML_PARTIALS from './html-partials/ENV.js';

// Add HTML_PARTIALS.params.importElement
// and HTML_PARTIALS.params.importElement as an innert context for SCOPED_JS
// This must be done before importing SCOPED JS exports
SCOPED_JS.params.inertContexts.push(
    HTML_PARTIALS.params.importElement, 
    HTML_PARTIALS.params.recomposeElement
);

// Configure HTML_PARTIALS to support SCOPED_HTML and SCOPED_JS
// This must be done before importing HTML PARTIALS exports
HTML_PARTIALS.params.SCOPED_HTML = SCOPED_HTML.params;
HTML_PARTIALS.params.SCOPED_JS = SCOPED_JS.params;

/**
 * @exports
 */
export {
    SCOPED_HTML,
    SCOPED_JS,
    HTML_PARTIALS,
};